.profile {
  text-align: center;
  position: relative;
  margin: auto;
  top: 17vh;
  max-width: 500px;
  padding-top: 20px;
  padding-bottom: 30px;
}

.profile__avatar {
  width: 280px;
  height: 280px;
  margin: auto;
  margin-bottom: 10px;
  object-fit: cover;
  border: 1px solid rgb(110, 110, 110);
  border-radius: 5px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.103);
}

.profile__username {
  margin: 10px;
}

.profile__total-orders {
  margin-bottom: 20px;
}

.profile__button {
  position: relative;
  left: 2px;
  font-weight: bold;
  background-color: #fccd4b;
  margin: 5px;
  margin-right: 15px;
  padding: 5px 10px;
  border: 1px solid #858585;
  border-radius: 20px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.219);
}

